import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { ActivityClassworkItemComponent } from './activity-classwork-item.component';
import { ActivityClassworkItemStudentComponent } from './activity-classwork-item-student/activity-classwork-item-student.component';


const routes: Routes = [
  { path: '', component: ActivityClassworkItemComponent,
    children: [
      { path: 'student/:studentid', component: ActivityClassworkItemStudentComponent }
    ]
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class ActivityClassworkItemRoutingModule { }
