import { Component, OnInit, ViewChild,TemplateRef, HostListener, ViewContainerRef, ViewEncapsulation } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';

import { Overlay, OverlayRef } from '@angular/cdk/overlay';
import { TemplatePortal } from '@angular/cdk/portal';
import { MatDialog } from '@angular/material/dialog';

import { ActivityClassworkItemComponent } from 'src/app/modules/admin/activity/activity-classwork/activity-classwork-item/activity-classwork-item.component';

import { ActivityClassworkService } from 'src/app/modules/admin/activity/activity-classwork/activity-classwork.service';

import * as moment from 'moment';

@Component({
  selector: 'app-activity-classwork-item-student',
  templateUrl: './activity-classwork-item-student.component.html',
  styleUrls: ['./activity-classwork-item-student.component.scss'],
  encapsulation: ViewEncapsulation.None
})
export class ActivityClassworkItemStudentComponent implements OnInit {

  student:any = {}

  activity:any = {}

  @ViewChild("submitClassWorkRef", { static: false }) submitClassWorkRef: TemplateRef<any>;

  constructor(
    private _overlay: Overlay,
    private _viewContainerRef: ViewContainerRef,
    public dialog: MatDialog,
    private _router: Router,
    private _activatedRoute: ActivatedRoute,
    private _activityClassworkItemComponent: ActivityClassworkItemComponent,
    private activityClassworkService: ActivityClassworkService
  ) { }

  goBack() {
    this._activityClassworkItemComponent.matDrawer.close();
    this.activityClassworkService.setClassWorkActivityStudentData(null)
    this._router.navigateByUrl('/activity/classwork/list/details/'+this.activity.id);
  }

  getDate(date) {
    return moment(date).format('Do MMM YYYY');
  }

  openSubmitModel() {

    this.dialog.open(this.submitClassWorkRef, {
      panelClass: 'material-dialog-medium'
    });

  }

  submitStudentActivityForm() {

    this.dialog.closeAll()

  }

  ngOnInit(): void {

    this.activityClassworkService.classworkactivitystudentdatacast.subscribe((res:any) => {
      if(res != null){
        this._activityClassworkItemComponent.matDrawer.open();
        this.student = res.data
      }
    })

    this.activityClassworkService.classworkactivitydatacast.subscribe((res:any) => {
      if(res != null){
        this.activity = res
      }
    })

  }

}
